import request from '@/utils/request';

interface LoginParams {
  user_id: string;
  user_pwd: string;
}
export interface RouteInfo {
  /**  */
  id: string;
  /**  */
  title: string;
  /**  */
  path?: string;
  /**  */
  children?: RouteInfo[];
  /**  */
  hidden?: Boolean;
  /**  id */
  activeMenu?: string;
  /**  */
  element?: string;
}
export interface LoginResult {
  token: string;
  userInfo: {
    routeList: RouteInfo[];
    [prop: string]: any;
  };
}

export const login = (params: LoginParams) => {
  return request<LoginResult>({
    url: '/public/login',
    method: 'POST',
    data: params,
  });
};
