import { throttle } from './index';

type ResizeFn = (w: number) => any;
interface ResizeOptions {
  // 
  immediate: boolean;
}

let resizeSet = new Set<ResizeFn>();
let eventHandle: () => void;

/**
 *  resize 
 * @param fn 
 * @returns 
 */
export function onResize(fn: ResizeFn, options?: ResizeOptions): () => void {
  if (resizeSet.size === 0) {
    // 
    window.addEventListener(
      'resize',
      (eventHandle = throttle(() => {
        resizeSet.forEach((fnVal) =>
          fnVal(document.documentElement.clientWidth)
        );
      }, 200))
    );
  }
  resizeSet.add(fn);

  // 
  if (options?.immediate) {
    fn(document.documentElement.clientWidth);
  }

  // 
  return () => {
    // 
    if (resizeSet.has(fn)) {
      resizeSet.delete(fn);
      if (resizeSet.size === 0) {
        //  resize 
        window.removeEventListener('resize', eventHandle);
      }
    }
  };
}
