import { Form, Input, Button } from 'antd';
import { useMemo, useState } from 'react';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { login } from '@/api';
import { setToken, setUserInfo } from '@/utils/localStore';
import { type Rule } from 'antd/lib/form';
import { useSetRecoilState } from 'recoil';
import './index.scss';
import { user_info_recoil } from '@/store/user';
import { useLocation, useNavigate, useSearchParams } from 'react-router-dom';

/**  start */
interface Rules {
  [prop: string]: Rule[];
}
/**  end */

/**  */
const rules: Rules = {
  username: [{ required: true, message: '' }],
  password: [{ required: true, message: '' }],
};
/**  end */

export default function Login() {
  const [isSubmit, setIsSubmit] = useState<boolean>(false); // 
  const [form] = Form.useForm();
  const setUserInfoRecoil = useSetRecoilState(user_info_recoil);
  const navigate = useNavigate();
  const [searchParams] = useSearchParams();
  const location = useLocation();
  // 
  const toRouteParams = useMemo(() => {
    let path = searchParams.get('form') || '/';
    let state = location.state;

    return {
      path,
      state,
    };
  }, [searchParams, location]);

  // 
  const onSubmit = async function () {
    setIsSubmit(true);

    try {
      const params = await form.validateFields();

      const data = await login(params);

      //  token 
      setToken(data.token);

      //  recoil  localStorage 
      setUserInfo(data.userInfo);
      setUserInfoRecoil(data.userInfo);

      // 
      navigate(toRouteParams.path, {
        state: toRouteParams.state,
        replace: true,
      });
    } catch (e) {
      // error -- Warning: Can't perform a React state update on an unmounted component. This is a no-op, but it indicates a memory leak in your application. To fix, cancel all subscriptions and asynchronous tasks in a useEffect cleanup function.
      // 
      setIsSubmit(false);
    }
  };

  return (
    <div className='_login_'>
      <h3 className='login_title'></h3>
      <Form
        /**  id 	 */
        name='login_form'
        size='large'
        /** label  */
        // labelCol={{ span: 8 }}
        /**  labelCol	 */
        // wrapperCol={{ span: 16 }}
        /** 	 */
        initialValues={{ username: 'admin', password: '123456' }}
        /**  autocomplete  */
        autoComplete='off'
        /**  Form.useForm()  form  */
        form={form}
        className='login_form'>
        <Form.Item name='username' rules={rules.username}>
          <Input placeholder='' prefix={<UserOutlined />} />
        </Form.Item>

        <Form.Item name='password' rules={rules.password}>
          <Input.Password placeholder='' prefix={<LockOutlined />} />
        </Form.Item>

        <Form.Item>
          <Button
            loading={isSubmit}
            type='primary'
            htmlType='button'
            onClick={onSubmit}>
            
          </Button>
        </Form.Item>
      </Form>
    </div>
  );
}
